/* AddCouncilPersonModal CSS */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  border: 4px solid black;
  box-shadow: 8px 8px 0 #000;
  padding: 30px;
  max-width: 600px;
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  font-family: "Press Start 2P", cursive;
}

.modal-content h2 {
  text-align: center;
  font-size: 14px;
  margin: 0 0 20px 0;
  font-family: "Press Start 2P", cursive;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-size: 10px;
  font-family: "Press Start 2P", cursive;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 10px;
  border: 2px solid black;
  font-family: "Press Start 2P", cursive;
  font-size: 10px;
  background: white;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: blue;
  box-shadow: 0 0 0 2px rgba(0, 0, 255, 0.2);
}

.avatar-selection {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  margin-top: 10px;
  padding: 10px;
  background: #f9f9f9;
  border: 2px solid black;
}

.avatar-option {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  padding: 10px;
  border: 2px solid black;
  background: white;
  cursor: pointer;
  font-family: "Press Start 2P", cursive;
  transition: all 0.1s;
  min-height: 80px;
}

.avatar-option:hover {
  transform: translate(-2px, -2px);
  box-shadow: 4px 4px 0 #000;
}

.avatar-option.selected {
  background: black;
  color: white;
  transform: translate(-2px, -2px);
  box-shadow: 4px 4px 0 #000;
}

.avatar-option .icon {
  font-size: 20px;
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: #f0f0f0;
  border: 2px solid black;
  margin: 0 auto;
}

.avatar-option .icon-name {
  font-size: 8px;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.modal-buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 20px;
}

.save-button {
  padding: 10px 20px;
  background: black;
  color: white;
  border: none;
  font-family: "Press Start 2P", cursive;
  font-size: 10px;
  cursor: pointer;
  box-shadow: 4px 4px 0 #666;
  transition: all 0.1s;
}

.save-button:hover {
  transform: translate(-2px, -2px);
  box-shadow: 6px 6px 0 #666;
}

.save-button:disabled {
  background: #ccc;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.cancel-button {
  padding: 10px 20px;
  background: white;
  color: black;
  border: 2px solid black;
  font-family: "Press Start 2P", cursive;
  font-size: 10px;
  cursor: pointer;
  box-shadow: 4px 4px 0 #666;
  transition: all 0.1s;
}

.cancel-button:hover {
  transform: translate(-2px, -2px);
  box-shadow: 6px 6px 0 #666;
}

.cancel-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}
